<template>
    <div class="">
        <c-data-tables title="Reporte de ventas" btns footer>
            <template v-slot:table>
                    <div class="form-inline form-group">
                        <div class="form-group">
                            <label>Fecha Inicio</label>
                            <input v-model="values.inicio" type="date" class="form-control">
                        </div>
                        <div class="form-group">
                            <label>Fecha Fin</label>
                            <input v-model="values.fin" type="date" class="form-control">
                        </div>
                        <button @click="send" class="btn btn-primary">
                            Buscar
                        </button>
                        <button @click="imprimir" :disabled="model.length==0" class="btn btn-success pull-right">
                            Generar Reporte
                            <span class="fa fa-download"></span>
                        </button>
                    </div>
                <table class="table table-condensed table-sm table-hover">
                    <thead>
                        <tr>
                            <th class="col-xs-1">Codigo</th>
                            <th class="col-md-3">Cliente</th>
                            <th>Importe</th>
                            <th style="width:40px">Dcto</th>
                            <th>SubTotal</th>
                            <th>IVA</th>
                            <th>Total</th>
                            <th class="col-xs-1">Cobró</th>
                            <th>Modo</th>
                            <th>Fecha</th>
                            <th></th>
                        </tr>
                    </thead>
                    <tbody>
                    <tr v-for="(m) in model" :key="m.id">
                        <td>{{m.id}}</td>
                        <td>{{m.cliente.nombre}} ( <b>{{m.cliente.telefono}}</b> )</td>
                        <td>{{m.importe}}</td>
                        <td>{{m.descuento==null?'0':m.descuento}}</td>
                        <td>{{m.sub_total}}</td>
                        <td>{{m.iva}}</td>
                        <td>{{m.total}}</td>
                        <td>{{m.cobrado_por}}</td>
                        <td>
                            <span v-if="m.modo==='normal'" class="label label-success">Normal</span>
                            <span v-else-if="m.modo==='urg'" class="label label-danger">Urgencia</span>
                            <span v-else-if="m.modo==='m1'" class="label label-warning">Maquila 1</span>
                            <span v-else-if="m.modo==='m2'" class="label label-info">Maquila 2</span>
                        </td>
                        <td>{{m.created_at}}</td>
                    </tr>
                    </tbody>
                </table>
            </template>
        </c-data-tables>
    </div>
</template>

<script>
    import api from '@/services'
    import session from '@/lib/session'

    export default {
        name: "ventas",
        data(){
            return {
                model:[],
                values: {
                    inicio:null,
                    fin:null
                },
                url:null
            }
        },
        mounted(){
            this.$emit('title','Reporte de ventas');
        },
        methods: {
            imprimir(){
                let token = session.getKey('session_lab').api_token;
                window.open(`${window.base_url}/reporte/pdf?inicio=${this.values.inicio}&fin=${this.values.fin}&api_token=${token}`);
            },
            limpiar(){
                this.values.inicio = null;
                this.values.fin = null;
            },
            send(){
                api.ventas.reportes(this.getParams()).then((r)=>{
                    if (r.status ===200){
                        this.model = r.data.data;
                        this.total = r.data.total;
                    }
                }).catch(()=>{});
            },
            getParams(){
                return {params:this.values};
            }
        }
    }
</script>